<template>
  <div class="auth-manage">
    <transition :enter-active-class="routerTransitionClass.enter"
                :leave-active-class="routerTransitionClass.leave">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  import RouterTransition from '@/mixins/router-transition'

  export default {
    name: "auth-manage",
    mixins:[
      RouterTransition
    ]
  }
</script>

<style scoped>
  .auth-manage {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    overflow-y: auto;
  }
</style>



